body {
  height: 100vh;
  background-color: #080e26;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin: 60px;
  display: flex;
}

.search-form {
  display: flex;
  position: relative;
}

.search-form input[type="text"] {
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 30px;
  outline: none;
  padding: 12px;
  font-size: 16px;
  transition: border-color .5s ease-out, box-shadow .5s ease-out;
  box-shadow: 0 3px 10px rgba(0, 0, 0, .5);
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 30px;
  outline: none;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  position: relative;
}

.search-form button[type="submit"]:before, .search-form input[type="text"]:before {
  content: "";
  opacity: 0;
  border: 2px solid #01f7ff;
  border-radius: 30px;
  transition: opacity .5s ease-out;
  position: absolute;
  top: -5px;
  bottom: -5px;
  left: -5px;
  right: -5px;
}

.search-form button[type="submit"]:hover, .search-form input[type="text"]:hover {
  border-color: #01f7ff;
  box-shadow: 0 3px 20px rgba(1, 179, 255, .7);
}

.search-form button[type="submit"]:hover:before, .search-form input[type="text"]:hover:before {
  opacity: 1;
  animation: glowing-border 1s ease-out infinite alternate;
}

@keyframes glowing-border {
  from {
    box-shadow: 0 0 10px #01f7ff, 0 0 20px #01f7ff, 0 0 30px #01f7ff, 0 0 40px #01f7ff, 0 0 50px #01f7ff, 0 0 60px #01f7ff;
  }

  to {
    box-shadow: 0 0 20px #01f7ff, 0 0 30px #01f7ff, 0 0 40px #01f7ff, 0 0 50px #01f7ff, 0 0 60px #01f7ff, 0 0 70px #01f7ff;
  }
}

